import React from 'react'

interface RichTextProps {
    title: string;
    titleClass?: string;
    text: string;
    textClass?: string;
}

function RichText({ title, titleClass = "", text, textClass = "" }: RichTextProps) {
  return (
    <div className={`font-extrabold text-6xl font-outfit py-6 ${titleClass}`}>
        {title}
        <p className={` font-light text-base font-outfit ${textClass}`}>
            {text}
        </p>
    </div>
  )
}

export default RichText